import React from "react";
import { connect } from "react-redux";
import {
  Todo,
  Auth,
  fetchTodos,
  deleteTodo,
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
} from "../../actions";
import { StoreState } from "../../reducers";
import { validateEmail } from "../../utils/validation";
import { Link, Redirect } from "react-router-dom";
import Alert, { AlertInterface } from "../../components/Alert/Alert";
import ConfirmationCode from "../../components/ConfirmationCode/ConfirmationCode";
import BgImage from "../../assets/BgVector.png";
// props for the component
interface AppProps {
  todos: Todo[];
  auth: Auth;
  fetchTodos: Function;
  deleteTodo: typeof deleteTodo;
  fetchAuth: Function;
  FC_GetTwoFactorAuth: Function;
  FC_ChangeTwoFactorType: typeof FC_ChangeTwoFactorType;
  FC_Login: Function;
  FC_LoginCheckCode: Function;
}

interface AppState {
  email: string;
  password: string;
  loading: boolean;
  res: any;
  error: {
    target: string;
    msg: string;
  };
  login_pass: boolean;
  code_loading: boolean;
  redirect: boolean;
}

class _Login extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      email: "",
      password: "",
      res: null,
      error: { target: "", msg: "" },
      login_pass: false,
      code_loading: false,
      redirect: false,
    };
  }

  componentDidMount = () => {
    if (this.props.auth.two_factor_auth.loaded === false) {
      this.props.FC_GetTwoFactorAuth();
    }
  };

  loginHandler = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    // check for errors
    if (this.state.email.length <= 4 || !validateEmail(this.state.email)) {
      return this.setState({
        error: {
          target: "email",
          msg: "Please enter a email",
        },
      });
    }

    // check for errors
    if (this.state.password.length <= 4) {
      return this.setState({
        error: {
          target: "password",
          msg: "Please enter your password",
        },
      });
    }

    this.setState({
      loading: true,
      error: {
        target: "",
        msg: "",
      },
      login_pass: false,
    });
    // * Login API
    this.props.FC_Login(
      {
        email: this.state.email,
        password: this.state.password,
        verify_type: this.props.auth.two_factor_auth.type,
      },
      (status: boolean, msg: string) => {
        if (status === true) {
          this.setState({
            loading: false,
            error: {
              target: "",
              msg: "",
            },
            login_pass: true,
          });
        } else {
          this.setState({
            loading: false,
            error: {
              target: "all",
              msg: msg,
            },
            login_pass: false,
          });
        }
      }
    );
  };

  handleChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
    this.setState({
      ...this.state,
      error: {
        target: "",
        msg: "",
      },
      [e.currentTarget.name]: e.target.value,
    } as Pick<AppState, keyof AppState>);
  };

  changeAuthType = (e: React.ChangeEvent<HTMLSelectElement>) =>
    this.props.FC_ChangeTwoFactorType(e.target.value);

  // check code
  checkCodeHandler = (verr_code: number) => {
    this.setState({
      code_loading: true,
    });

    this.props.FC_LoginCheckCode(
      { email: this.props.auth.user.email, verification_code: verr_code },
      (status: boolean, msg: string) => {
        if (status === true) {
          this.setState({
            redirect: true,
          });
        } else {
          this.setState({
            error: {
              target: "all",
              msg: msg,
            },
            code_loading: false,
          });
        }
      }
    );
  };

  backgroundStyle = {
    backgroundImage: `url(${BgImage})`,
    backgroundPosition: "center",
    backgroundSize: "cover",
    backgroundAttachment: "fixed",
  };

  render() {
    if (
      this.state.redirect === true ||
      this.props.auth.isAuthenticated === true
    ) {
      return <Redirect to="/dashboard" />;
    }
    // check if is the user is the supervisor

    return (
      <div style={this.backgroundStyle}>
        <div className="max-w-7xl mx-auto px-2 sm:px-6 lg:px-24  h-screen flex flex-wrap content-center">
          <div className="grid lg:grid-cols-5 w-full">
            <div className="col-span-3 hidden lg:block">
              <h1 className="text-7xl font-serif">
                Online Teacher <br /> Recruitment <br /> Platform
              </h1>
              <h4 className="text-blue-600 text-3xl w-2/3 mt-2">
                For all public schools under Rwanda Basic Education Board and
                TVET
              </h4>
            </div>

            <div className="col-span-2">
              {this.state.login_pass === true ? (
                <div className="">
                  {this.state.error.target === "all" && (
                    <div className="mx-auto w-full">
                      <Alert
                        title={this.state.error.msg}
                        theme={AlertInterface.danger}
                      />
                    </div>
                  )}
                  <ConfirmationCode
                    user={this.props.auth.user}
                    code_loading={this.state.code_loading}
                    submitCode={this.checkCodeHandler}
                  />
                </div>
              ) : (
                <div className="bg-white bg-opacity-70 shadow mx-auto px-4 py-5 rounded max-w-xl mt-6 sm:mx-auto">
                  <form onSubmit={this.loginHandler}>
                    <h1 className="px-2 font-extrabold text-4xl text-blue-500  pt-2 pb-4">
                      Login
                    </h1>

                    {this.state.error.target === "all" && (
                      <div className="p-2">
                        <Alert
                          title={this.state.error.msg}
                          theme={AlertInterface.danger}
                        />
                      </div>
                    )}

                    <div className="p-2">
                      <label htmlFor="email" className="d-block">
                        Email
                      </label>
                      <input
                        type="text"
                        name="email"
                        onChange={this.handleChange}
                        className={`input-white w-20 inline-block  ${
                          this.state.error?.target === "email"
                            ? "border-red-500 border-2"
                            : "input-border"
                        }`}
                        placeholder="Your email"
                        value={this.state.email}
                        disabled={this.state.loading}
                      />
                      {this.state.error?.target === "email" && (
                        <span className="text-red-600">
                          {this.state.error?.msg}
                        </span>
                      )}
                    </div>

                    <div className="p-2">
                      <label htmlFor="username" className="d-block">
                        Password
                      </label>
                      <input
                        type="password"
                        name="password"
                        onChange={this.handleChange}
                        className={`input-white w-20 inline-block  ${
                          this.state.error?.target === "password"
                            ? "border-red-500 border-2"
                            : "input-border"
                        }`}
                        placeholder="Your username"
                        value={this.state.password}
                        disabled={this.state.loading}
                      />
                      {this.state.error?.target === "password" && (
                        <span className="text-red-600">
                          {this.state.error?.msg}
                        </span>
                      )}
                    </div>

                    {this.props.auth.two_factor_auth.active === true && (
                      <div className="p-1 border-b mt-2 border-gray-2 pb-2 my-1 mx-3">
                        <h3 className="text-lg font-bold m-0 text-gray-500">
                          Two step auth is active
                        </h3>
                        <section className="text-sm">
                          <p className="text-gray-400 inline-block">
                            Send me the code on{" "}
                          </p>
                          <select
                            className="inline-block ml-2 bg-blue-200 rounded text-blue-800 font-bold"
                            name="auth_type"
                            onChange={this.changeAuthType}
                          >
                            <option value="email">Email</option>
                            <option value="sms">SMS</option>
                          </select>
                        </section>
                      </div>
                    )}

                    <div className="p-2">
                      <div className="flex mt-2">
                        <div className="flex-1 ">
                          <Link
                            to="/register"
                            className="font-bold text-blue-600 underline"
                          >
                            Create account
                          </Link>
                        </div>

                        <div className="flex-1  text-right">
                          <button
                            className="py-2 font-bold  px-8 rounded bg-hero text-white inline-block"
                            disabled={this.state.loading}
                            type="submit"
                          >
                            {this.state.loading === true
                              ? "Loading..."
                              : "Login"}
                          </button>
                        </div>
                      </div>

                      <div className="mt-7 text-center">
                        <Link
                          to="/forget-pass"
                          className="text-gray-500 hover:text-blue-600 hover:underline"
                        >
                          Forget password
                        </Link>
                      </div>
                    </div>
                  </form>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({
  todos,
  auth,
}: StoreState): { todos: Todo[]; auth: Auth } => {
  return { todos: todos, auth: auth };
};

export const Login = connect(mapStateToProps, {
  fetchTodos,
  deleteTodo,
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
})(_Login);
