import React, { Component } from 'react'
import { BiChat } from 'react-icons/bi';
import { FaMicrophone } from 'react-icons/fa';
import { FiVolume2 } from 'react-icons/fi';
import { IoDocumentTextOutline, IoNotifications } from 'react-icons/io5';
import { MdCheck, MdCheckBoxOutlineBlank, MdCheckCircle, MdChevronLeft, MdChevronRight, MdModeEdit, MdRadioButtonUnchecked } from 'react-icons/md';
import { ApplicationStepsTypes } from '../ApplicationSteps/ApplicationSteps';
import Footer from './Footer/Footer';
import Profile from './Profile/Profile';
import QuestionDetails from './QuestionDetails/QuestionDetails';
import QuestionsList, { psychometric_questions_list } from './QuestionsList/QuestionsList';
import TestTitle from './TestTitle/TestTitle';
import * as Interfaces from '../../../interfaces/index';
import { connect } from "react-redux";
import {
  Todo,
  Auth,
  fetchTodos,
  deleteTodo,
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
  getUserAnswer,
  addAnswer,
  getUsersAnswers,
  addUsersAnswers,
  questionAnswerInterface,
  ActionTypes,
  SubmitAnswers,
} from "../../../actions";
import { StoreState } from "../../../reducers";
import { question_answer } from '../../../interfaces/index';
import DoTestResults from './DoTestResults/DoTestResults';

interface DoTestProps {
  currentPosition: Interfaces.PositionAPI | null;
  setCurrentPosition: (position: Interfaces.PositionAPI | null) => void;
  setApplicationStep: (step: ApplicationStepsTypes) => void;
  getUserAnswer: Function;
  addAnswer: (questionAnswer: newAnswer) => addUsersAnswers;
  doTest: questionAnswerInterface;
  auth: Auth;
  SubmitAnswers: Function;
}

interface DoTestState {
  windowStatus: {
    profile: boolean,
    questionsList: boolean,
  };
  selectedQuestion:Interfaces.psychometric_question | null;
  testAnswers: newAnswer[] | [];
  completedTest: boolean;
}

export interface newAnswer {
  question_id: string | null;
  user_answers: question_answer | null
}

class _DoTest extends Component<DoTestProps, DoTestState> {
  constructor(props: DoTestProps) {
		super(props);

		this.state = {
      windowStatus: {
        profile: false,
        questionsList: false,
      },
      selectedQuestion: psychometric_questions_list[0],
      testAnswers: [],
      completedTest: false,
		};
	}
  setProfileWindowStatus = (status: boolean) => {
    this.setState({ windowStatus: { profile: status, questionsList: this.state.windowStatus.questionsList }})
  }
  setQuestionsListWindowStatus = (status: boolean) => {
    this.setState({ windowStatus: { profile: this.state.windowStatus.profile, questionsList: status }})
  }

  setSelectedQuestion = (question: Interfaces.psychometric_question) => {
    this.setState({ selectedQuestion:  question});
  }

  answerQuestionFn = (question_id: string, answer: question_answer) => {
    console.log("Input: ", {
      question_id: question_id,
      user_answers: answer
    })
    let current: newAnswer[] = this.state.testAnswers.length > 0 ? this.state.testAnswers.filter(itm => itm.question_id !== question_id) : this.state.testAnswers;
    current.push({
      question_id: question_id,
      user_answers: answer
    });
    this.setState({ 
      testAnswers: current
    })
    for (let i in current) {
      this.props.addAnswer(current[i]);
    }
    
    console.log("Answers: ", this.state.testAnswers);
  }

  setCompletedTest = (state: boolean) => {
    this.setState({ completedTest: state })
  }

  initializeEmptyList = () => {
    // localStorage.removeItem(ActionTypes.ADD_ANSWER)
    let testResults = localStorage.getItem(ActionTypes.ADD_ANSWER);
    if (testResults !== null) {
      let newData = JSON.parse(testResults!).questionAnswers;
      if (newData.length > 0) {
        for (let i in newData) {
          this.props.addAnswer(newData[i]);
        }
        if (newData.length === psychometric_questions_list.length) {
          this.setState({ completedTest: true });
          this.props.SubmitAnswers(this.props.auth.user.n_id);
        }
      }
    }
  }

  componentDidMount = () => {
    this.initializeEmptyList();
    if (this.props.doTest.questionAnswers.length === 0 && this.props.doTest.questionAnswers.length > 0) {
      this.setState({ testAnswers: this.props.doTest.questionAnswers })
    }
  }

  render() {
    return (
      this.state.completedTest === true ? 
      <DoTestResults
        answeredQuestions={this.props.doTest.questionAnswers}
        testAnswers={this.props.doTest.questionAnswers.length}
        currentPosition={this.props.currentPosition}
        setCurrentPosition={this.props.setCurrentPosition}
        setApplicationStep={this.props.setApplicationStep}
        windowStatus={this.state.windowStatus}
        setProfileWindowStatus={this.setProfileWindowStatus}
        setQuestionsListWindowStatus={this.setQuestionsListWindowStatus}
        setSelectedQuestion={this.setSelectedQuestion}
      />
      :
      <div className="fixed w-full h-screen bg-blue-900 overflow-y-hidden">
        <div className="grid md:grid-cols-12 h-full">
          {this.state.windowStatus.profile === true &&
            <div className={`col-span-6 lg:col-span-2 fixed left-0 md:relative`}>
              <Profile 
                setProfileWindowStatus={this.setProfileWindowStatus}
              />
            </div>
          }
          <div className={`col-span-${this.state.windowStatus.profile === false ? "12" : "10"} bg-gray-800 shadow-lg h-full`}>
            <div className="flex flex-row h-screen bg-gray-800">
              <div className="h-full">
                {/* Questions list */}
                <div className={`h-full`}>
                  <QuestionsList 
                    answeredQuestions={this.props.doTest.questionAnswers}
                    testAnswers={this.props.doTest.questionAnswers.length}
                    windowStatus={this.state.windowStatus}
                    setProfileWindowStatus={this.setProfileWindowStatus}
                    setQuestionsListWindowStatus={this.setQuestionsListWindowStatus}
                    selectedQuestion={this.state.selectedQuestion}
                    setSelectedQuestion={this.setSelectedQuestion}
                  />
                </div>
              </div>
              <div className="w-full h-full">
                {/* Do exam test title */}
                <TestTitle
                  setCompletedTest={this.setCompletedTest}
                  answeredQuestions={this.props.doTest.questionAnswers}
                  testAnswers={this.props.doTest.questionAnswers.length}
                  currentPosition={this.props.currentPosition}
                  setCurrentPosition={this.props.setCurrentPosition}
                  setApplicationStep={this.props.setApplicationStep}
                  windowStatus={this.state.windowStatus}
                  setProfileWindowStatus={this.setProfileWindowStatus}
                  setQuestionsListWindowStatus={this.setQuestionsListWindowStatus}
                  setSelectedQuestion={this.setSelectedQuestion}
                />
                {/* Question details */}
                <QuestionDetails 
                  questionAnswers={this.props.doTest.questionAnswers}
                  answerQuestionFn={this.answerQuestionFn}
                  currentPosition={this.props.currentPosition}
                  setCurrentPosition={this.props.setCurrentPosition}
                  setApplicationStep={this.props.setApplicationStep}
                  selectedQuestion={this.state.selectedQuestion}
                  setSelectedQuestion={this.setSelectedQuestion}
                />
              </div>
            </div>
          </div>
        </div>
        <Footer 
          selectedQuestion={this.state.selectedQuestion}
          setSelectedQuestion={this.setSelectedQuestion}
          currentPosition={this.props.currentPosition}
          setCurrentPosition={this.props.setCurrentPosition}
          setApplicationStep={this.props.setApplicationStep}
          windowStatus={this.state.windowStatus}
          setProfileWindowStatus={this.setProfileWindowStatus}
          setQuestionsListWindowStatus={this.setQuestionsListWindowStatus}
        />
      </div>
    )
  }
}

const mapStateToProps = ({
  todos,
  auth,
  doTest,
}: StoreState): { todos: Todo[]; auth: Auth; doTest: questionAnswerInterface } => {
  return { todos: todos, auth: auth, doTest: doTest };
};

const DoTest = connect(mapStateToProps, {
  fetchTodos,
  deleteTodo,
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
  getUserAnswer,
  addAnswer,
  SubmitAnswers,
})(_DoTest);

export default DoTest;