import { combineReducers } from "redux";
import { todosReducer } from "./todos";
import { authReducer } from "./auth";

import { Todo, Auth, Socket, psychometricAnswer, questionAnswerInterface } from "../actions";
import { socketReducer } from "./socket";

import { doTestReducer } from "./doTest"
import { psychometric_question, question_answer,  } from "../interfaces";
import { newAnswer } from "../containers/Application/DoTest/DoTest";

// define the entire state into the entire sire
export interface StoreState {
  todos: Todo[];
  auth: Auth;
  socket: Socket;
  doTest: questionAnswerInterface;
}

export const reducers = combineReducers<StoreState>({
  todos: todosReducer,
  auth: authReducer,
  socket: socketReducer,
  doTest: doTestReducer,
});
