import React from "react";
import axios from "axios";
// props for the component
interface AppProps {}

enum Selected {
  Progress,
  Completed,
  Development,
}
interface DataItem {
  id: string;
  title: string;
  decription: string;
  done_by: string;
  progress: number;
  complete: boolean;
}

interface AppState {
  loading: boolean;
  selected: Selected;
  data: DataItem[];
  error: boolean;
}

class SystemDevelopmentProgress extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: true,
      selected: Selected.Progress,
      data: [],
      error: false,
    };
  }

  componentDidMount = () => {
    this.getData();
  };

  getData = async () => {
    try {
      if (this.state.data.length >= 1) {
        this.setState({ loading: false });
        return false;
      }

      let DATA = [];
      const res = await axios.get(
        "https://react-my-burger-62074.firebaseio.com/mastercard-development-progress.json"
      );
      console.log({ progress: res.data });

      for (const key in res.data) {
        // console.log(`${key}: ${res.data[key]}`);
        DATA.push({
          ...res.data[key],
          id: key,
        });
      }

      this.setState({
        data: DATA,
        loading: false,
        error: false,
      });
    } catch (error) {
      this.setState({
        data: [],
        loading: false,
        error: false,
      });
    }
  };

  selectHandler = (value: Selected) => this.setState({ selected: value });

  render() {
    let TotalFeatures = this.state.data.length;
    let DevelopmentFeatures = this.state.data.filter(
      (itm: DataItem) => itm.complete === false
    ).length;
    let CompletedFeatures = TotalFeatures - DevelopmentFeatures;

    let totla_percentage = 0;
    this.state.data.forEach((itm: DataItem) => {
      totla_percentage = totla_percentage + itm.progress;
    });

    let Progress = (
      (totla_percentage / (this.state.data.length * 100)) *
      100
    ).toFixed();

    return (
      <div className="container rounded bg-white shadow-xl border-blue-500 border mx-auto">
        <div className="px-3 pt-3 text-center bg-blue-100 border-b border-blue-500">
          <h1 className="text-blue-600 font-bold text-4xl">
            Development Progress
          </h1>

          <div className="mt-2  ">
            <button
              onClick={() => this.selectHandler(Selected.Progress)}
              className={`${
                this.state.selected === Selected.Progress
                  ? "border-b-2 border-blue-700 bg-blue-500 text-white font-bold"
                  : "text-blue-700"
              } p-2 rounded-t `}
            >
              Progress
            </button>
            <button
              onClick={() => this.selectHandler(Selected.Completed)}
              className={`${
                this.state.selected === Selected.Completed
                  ? "border-b-2 border-blue-700 bg-blue-500 text-white font-bold"
                  : "text-blue-700"
              } p-2 rounded-t `}
            >
              Completed features
            </button>
            <button
              onClick={() => this.selectHandler(Selected.Development)}
              className={`${
                this.state.selected === Selected.Development
                  ? "border-b-2 border-blue-700 bg-blue-500 text-white font-bold"
                  : "text-blue-700"
              } p-2 rounded-t `}
            >
              In-Development
            </button>
          </div>
        </div>

        {this.state.loading === true ? (
          <div className=" bg-white rounded py-9 text-center">
            <h1 className="text-center text-secondary my-5 animate-pulse text-gray-400 text-5xl font-bold">
              Loading...
            </h1>
          </div>
        ) : (
          <div className="p-2 bg-white rounded">
            {this.state.selected === Selected.Progress && (
              <div className="p-4">
                <div className="grid grid-cols-4 gap-2">
                  <div className="col-span-2 px-4">
                    <h4 className="text-2xl font-bold text-green-800">
                      System progress
                    </h4>
                    <h1 className="text-9xl text-green-600 font-bold">
                      {Progress}%
                    </h1>
                  </div>
                  <div>
                    {" "}
                    <div
                      className="py-2 bg-red-100 px-3 hover:bg-red-200 rounded text-red-700"
                      onClick={() => this.selectHandler(Selected.Development)}
                    >
                      <h5>In-Development</h5>
                      <h2 className="text-4xl font-bold">
                        {DevelopmentFeatures}
                      </h2>
                    </div>
                    <div
                      className="mt-2 py-2 bg-yellow-100 px-3  rounded text-yellow-700"
                      onClick={() => this.selectHandler(Selected.Progress)}
                    >
                      <h5>Total features</h5>
                      <h2 className="text-4xl font-bold">{TotalFeatures}</h2>
                    </div>
                  </div>
                  <div>
                    <div
                      className="py-2 bg-green-100 px-3 hover:bg-green-200 rounded text-green-700"
                      onClick={() => this.selectHandler(Selected.Completed)}
                    >
                      <h5>Completed features</h5>
                      <h2 className="text-4xl font-bold">
                        {CompletedFeatures}
                      </h2>
                    </div>
                  </div>
                </div>
                <div className="m-3 bg-green-200 p-2 rounded-full">
                  <div
                    className=" bg-green-600 py-2 rounded-full"
                    style={{ width: Progress + "%" }}
                  ></div>
                </div>
              </div>
            )}
            {this.state.selected === Selected.Completed && (
              <div className="">
                {this.state.data
                  .filter((el: DataItem) => el.complete === true)
                  .map((itm: DataItem) => (
                    <div
                      key={itm.id}
                      className="w-full p-2 bg-white shadow-md border border-blue-300 hover:bg-blue-100 mb-1 rounded"
                    >
                      <div className="grid grid-cols-7 gap-2 w-full">
                        <div className="col-span-6">
                          <h3 className="text-blue-700 text-xl font-bold">
                            {itm.title}
                          </h3>
                          <p className="text-gray-700 m-0">{itm.decription}</p>
                        </div>
                        <div className="text-right">
                          <h1 className="text-4xl pt-1 font-bold text-blue-700">
                            {itm.progress}%
                          </h1>
                        </div>
                      </div>
                      {itm.complete === false && (
                        <div className="mt-1 bg-blue-300 p-1 rounded-full">
                          <div
                            className=" bg-blue-600 py-1 rounded-full"
                            style={{ width: itm.progress + "%" }}
                          ></div>
                        </div>
                      )}
                    </div>
                  ))}
              </div>
            )}
            {this.state.selected === Selected.Development && (
              <div className="">
                {this.state.data
                  .filter((el: DataItem) => el.complete === false)
                  .map((itm: DataItem) => (
                    <div
                      key={itm.id}
                      className="w-full p-2 bg-white shadow-md border border-blue-300 hover:bg-blue-100 mb-1 rounded"
                    >
                      <div className="grid grid-cols-7 gap-2 w-full">
                        <div className="col-span-6">
                          <h3 className="text-blue-700 text-xl font-bold">
                            {itm.title}
                          </h3>
                          <p className="text-gray-700 m-0">{itm.decription}</p>
                        </div>
                        <div className="text-right">
                          <h1 className="text-4xl pt-1 font-bold text-blue-700">
                            {itm.progress}%
                          </h1>
                        </div>
                      </div>
                      {itm.complete === false && (
                        <div className="mt-1 bg-blue-300 p-1 rounded-full">
                          <div
                            className=" bg-blue-600 py-1 rounded-full"
                            style={{ width: itm.progress + "%" }}
                          ></div>
                        </div>
                      )}
                    </div>
                  ))}
              </div>
            )}
          </div>
        )}
      </div>
    );
  }
}

export default SystemDevelopmentProgress;
