import React from 'react'
import { ApplicationStepsTypes } from '../../ApplicationSteps/ApplicationSteps'
import { newAnswer } from '../DoTest'
import * as Interfaces from '../../../../interfaces/index';
import { psychometric_questions_list } from '../QuestionsList/QuestionsList';
import { Link } from 'react-router-dom';

interface DoTestResultsInterface { 
  currentPosition: Interfaces.PositionAPI | null;
  setCurrentPosition: (position: Interfaces.PositionAPI | null) => void;
  setApplicationStep: (step: ApplicationStepsTypes) => void;
  windowStatus: {
    profile: boolean,
    questionsList: boolean,
  };
  setProfileWindowStatus: (status: boolean) => void;
  setQuestionsListWindowStatus: (status: boolean) => void;
  testAnswers: number;
  answeredQuestions: newAnswer[] | [];
  setSelectedQuestion: (question: Interfaces.psychometric_question) => void;
}

const DoTestResults = (props: DoTestResultsInterface) => {
  const findResult = () => {
    let marks: number = 0;
    for (let i in props.answeredQuestions) {
      let answer = props.answeredQuestions;
      if (answer[i].user_answers?.status === true) {
        marks = marks + 1;
      }
    }
    return (marks * 100)/psychometric_questions_list.length;
  }
  return (
    <div className="max-w-7xl mx-auto px-2 md:px-4 lg:px-24">
      <div className="mt-5 bg-white rounded shadow px-3 py-3">
      <div className="text-4xl font-bold text-blue-600">Psychometric Test results</div>
      <div className="text-sm text-gray-500"></div>
        <div className="my-2">
          <div className="font-bold text-lg text-gray-700 my-2 mt-4">Answers history</div>
          <div className="flex flex-col gap-3">
            {psychometric_questions_list.map((item, i) => (
              <div className="bg-gray-200 px-2 py-2 rounded">
                <div className="grid md:grid-cols-12 w-full">
                  <div className="col-span-10 flex flex-row items-center gap-3">
                    <div key={i + 1} className="w-6 h-6 p-2 rounded-full bg-gray-600 flex justify-center items-center font-bold text-lg text-white cursor-pointer">{item.question_id}</div>
                    <div className="text-sm">
                      <div>{item.question_description}</div>
                      <div className="text-blue-700 font-bold text-sm">Answer:&nbsp;
                        {props.answeredQuestions.find(itm => itm.question_id === item.question_id)?.user_answers?.answer}
                      </div>
                    </div>
                  </div>
                  {props.answeredQuestions.find(itm => itm.question_id === item.question_id)?.user_answers?.status === true ? 
                  <div className="col-span-2 bg-blue-100 rounded px-2 py-1 text-center float-right cursor-pointer font-bold text-md text-blue-600 border border-blue-400 flex items-center justify-center h-max md:ml-3">
                    Right
                  </div> : 
                  <div className="col-span-2 bg-yellow-100 rounded px-2 py-1 text-center float-right cursor-pointer font-bold text-md text-yellow-600 border border-yellow-400 flex items-center justify-center h-max md:ml-3">
                    Wrong
                  </div>}
                </div>
              </div>
            ))}

            <div className={`border-2 ${findResult() >= 50 ? "border-blue-400 bg-blue-100 text-blue-700" : "border-red-400 bg-red-100 text-gray-700"} px-3 py-3 rounded flex flex-row justify-between items-center`}>
              <div className="">
                <div className="text-left text-xl">Total marks</div>
                <div className="text-5xl">{findResult()}%</div>
              </div>
              {findResult() >= 50 ?
              <button onClick={() => props.setApplicationStep(ApplicationStepsTypes.Step1)} className="btn btn-blue">Continue</button>
              :
              <Link className="btn border border-gray-400 bg-white" to="/">Back to home</Link>}
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default DoTestResults
