import { Auth, Action, ActionTypes } from "../actions";
import { APP_TOKEN_NAME } from "../utils/AxiosToken";
// import {CandidateInterface} from '../interfaces/candidatesInterfaces'
// default state
const defaultState = {
  isAuthenticated: false,
  id: "",
  token: "",
  rules: [],
  two_factor_auth: {
    loaded: false,
    active: false,
    type: "email",
  },
  user: {
    user_id: "",
    n_id: "",
    fname: "",
    lname: "",
    date_of_birth: "",
    district: "",
    email: "",
    phone_number: "",
    stoped: false,
    active: false,
    // more details
    application_number: "",
    cell: "",
    civil_status: "",
    country_of_birth: "",
    date_of_expiry: "",
    date_of_issue: "",
    document_type: "",
    father_names: "",
    issue_number: "",
    mother_names: "",
    photo: "",
    place_of_birth: "",
    place_of_issue: "",
    province: "",
    sector: "",
    sex: "",
    spouse: "",
    status: "",
    village: "",
    village_id: "",
    // users events
    stopped: false,
    ref: null,
    muted: false,
    room_id: "",
    video: true,
    stream: false,
    peer: null,
    peer_id: "",
    type: "",
    id: "",
  },
  document_types: {
    categories: [],
    sub_categories: [],
  },
  documents: [],
} as Auth;

/**
 * this is the comment that im using from my brand new keyboard and i like i very mutch hahah this is awesome and coool
 * @param state
 * @param action
 * @returns
 */
export const authReducer = (state: Auth = defaultState, action: Action) => {
  switch (action.type) {
    case ActionTypes.LOGIN_DETAILS:
      return { ...state, ...action.payload };
    case ActionTypes.SET_TWO_FACTOR_AUTH:
      return {
        ...state,
        two_factor_auth: {
          ...state.two_factor_auth,
          loaded: true,
          active: action.payload,
        },
      } as Auth;

    case ActionTypes.USER_REGISTER_DETAILS:
      return {
        ...state,
        user: {
          ...state.user,
          ...action.payload,
        },
      } as Auth;
    case ActionTypes.CLEAN_USER_DETAILS:
      return {
        ...defaultState,
      };
    case ActionTypes.USER_LOGIN_DETAILS:
      return {
        ...state,
        user: {
          ...state.user,
          user_id: action.payload.user_id,
          fname: action.payload.fname,
          lname: action.payload.lname,
          email: action.payload.email,
        },
      } as Auth;
    case ActionTypes.USER_LOGIN_SUCCESS_DATA:
      return {
        ...state,
        token: action.payload.user_jwt,
        isAuthenticated: true,
        user: {
          ...state.user,
          n_id: action.payload.user_data.n_id,
          fname: action.payload.user_data.fname,
          lname: action.payload.user_data.lname,
          date_of_birth: action.payload.user_data.date_of_birth,
          district: action.payload.user_data.district,
          email: action.payload.user_data.email,
          phone_number: action.payload.user_data.phone_number,
          user_id: action.payload.user_data.user_id,
        },
      } as Auth;
    case ActionTypes.GET_MY_PROFILE_DETAILS:
      return {
        ...state,
        user: {
          ...state.user,
          ...action.payload.user_info,
        },
        document_types: {
          categories: action.payload.categeries,
          sub_categories: action.payload.subcategories,
        },
        documents: action.payload.details,
      } as Auth;
    case ActionTypes.UPLOAD_USER_DOCUMENT:
      return {
        ...state,
        documents: [...state.documents, action.payload],
      } as Auth;
    case ActionTypes.DELETE_USER_DOCUMENT:
      return {
        ...state,
        documents: state.documents.filter((el) => el.doc_id !== action.payload),
      } as Auth;
    case ActionTypes.LOGOUT:
      localStorage.removeItem(APP_TOKEN_NAME);
      return defaultState;
    default:
      return state;
  }
};
