import React from 'react'
import { MdCheckCircle, MdChevronLeft, MdChevronRight, MdRadioButtonUnchecked } from 'react-icons/md';
import { BiChat } from 'react-icons/bi'
import { ApplicationStepsTypes } from '../../ApplicationSteps/ApplicationSteps';
import { FaMicrophone } from 'react-icons/fa';
import { FiVolume2 } from 'react-icons/fi';
import * as Interfaces from '../../../../interfaces/index';
import { question_answer } from '../../../../interfaces/index';
import { newAnswer } from '../DoTest';

interface QuestionDetailsProps {
  currentPosition: Interfaces.PositionAPI | null;
  setCurrentPosition: (position: Interfaces.PositionAPI | null) => void;
  setApplicationStep: (step: ApplicationStepsTypes) => void;
  selectedQuestion: Interfaces.psychometric_question | null;
  setSelectedQuestion: (question: Interfaces.psychometric_question) => void;
  answerQuestionFn: (question_id: string, answer: question_answer) => void;
  questionAnswers: newAnswer[];
}

const QuestionDetails = (props: QuestionDetailsProps) => {
  return (
    <div className="bg-gray-800 h-full">
      <div className="bg-gray-200 px-3 py-2 rounded my-2 mr-2 h-full overflow-y-auto pb-80 md:pb-60">
          <div className="flex flex-row gap-2">
            <div className="h-8 w-8 bg-gray-500 flex justify-center items-center text-white rounded-full px-3 py-2">{props.selectedQuestion !== null && props.selectedQuestion.question_id}</div>
            <div className="text-md flex flex-col">
              <span className="font-bold">{props.selectedQuestion !== null && props.selectedQuestion.question_description}</span>
              <span className="italic text-xs">Choose the correct answer
              </span>
            </div> 
          </div>
          <div className="my-4 mx-3 py-2 px-2 bg-white rounded">
            {props.selectedQuestion !== null &&
            props.selectedQuestion.question_answers.map((item) => (
              <div onClick={() => props.answerQuestionFn(props.selectedQuestion!.question_id, item)} className={`px-2 py-2 border-b ${props.questionAnswers.length > 0 ? props.questionAnswers.find(itm => itm.question_id === props.selectedQuestion?.question_id)?.user_answers?.answer === item.answer ? "bg-blue-600 border-blue-500 text-blue-100 hover:bg-blue-800" : "bg-white border-gray-100 hover:bg-gray-300" : ""} flex flex-row justify-left items-center rounded cursor-pointer`}>
                <MdRadioButtonUnchecked className="" />
                <span className="ml-2">{item.answer}</span>
              </div>
            ))}
          </div>
      </div>
    </div>
  )
}

export default QuestionDetails
