const url = `/${API_URL}/todos`;

/**
 * * ****************************** INTERFACES *****************************
 */

export interface FetchPsychometricAnswerAction {
  type: ActionTypes.GET_USER_ANSWERS;
  payload: psychometricAnswer[];
}

export interface getUsersAnswers {
  type: ActionTypes.GET_USER_ANSWERS;
}

export interface addUsersAnswers {
  type: ActionTypes.ADD_ANSWER;
  payload: newAnswer;
}

