export interface questionAnswerInterfaceItem {
  user_id: string | null;
  question_id: string | null;
  user_answers: Interfaces.question_answer[];
}

export interface questionAnswerInterface {
  user_id?: string | null;
  questionAnswers: newAnswer[]
}

export interface psychometricAnswer {
  user_id?: string | null;
  questionAnswers: questionAnswerInterface[]
}

export interface newAnswerInterface {
  questionAnswers: newAnswer[]
}

export interface DeleteAnswersAction {
  type: ActionTypes.deleteAnswers
}

export interface SubmitAnswersAction {
  type: ActionTypes.submitAnswersAction;
  payload: string;
}

/**
 * * ****************************** ACTIONS *****************************
 */

export const getUserAnswer = (): getUsersAnswers => {
  return {
    type: ActionTypes.GET_USER_ANSWERS,
  };
};


export const addAnswer = (questionAnswer: newAnswer): addUsersAnswers => {
  return {
    type: ActionTypes.ADD_ANSWER,
    payload: questionAnswer,
  };
};

export const deleteAnswers = (): DeleteAnswersAction => {
  return {
    type: ActionTypes.deleteAnswers,
  };
};

export const SubmitAnswers = (user_id: string): SubmitAnswersAction => {
  return {
    type: ActionTypes.submitAnswersAction,
    payload: user_id,
  };
};
