import React from "react";
import { connect } from "react-redux";
import {
  Todo,
  Auth,
  FC_RegisterAccount,
  FC_RegisterCheckCode,
  FC_CleanUserDetails,
} from "../../actions";
import { StoreState } from "../../reducers";
import { validateEmail } from "../../utils/validation";
// import { Link } from "react-router-dom";
import Alert, { AlertInterface } from "../../components/Alert/Alert";
// import RegisterAccountVerification from "./RegisterAccountVerification";
import BgImage from "../../assets/BgVector.png";
import ConfirmationCode from "../../components/ConfirmationCode/ConfirmationCode";
import { Link } from "react-router-dom";
import { MdCheckCircle } from "react-icons/md";

// props for the component
interface AppProps {
  auth: Auth;
  FC_RegisterAccount: Function;
  FC_RegisterCheckCode: Function;
  FC_CleanUserDetails: Function;
}

interface AppState {
  n_id: string;
  phone_number: string;
  email: string;
  password: string;
  re_password: string;
  loading: boolean;
  error: {
    target: string;
    msg: string;
  };
  register_success: boolean;
  code_loading: boolean;
}

class _RegisterAccount extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      n_id: "",
      phone_number: "",
      email: "",
      password: "",
      re_password: "",
      error: { target: "", msg: "" },
      register_success: false,
      code_loading: false,
    };
  }

  registerHandler = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    // check for errors
    if (this.state.n_id.length <= 4) {
      return this.setState({
        error: {
          target: "n_id",
          msg: "NID is required",
        },
      });
    }

    // check for errors
    if (this.state.phone_number.length <= 4) {
      return this.setState({
        error: {
          target: "phone_number",
          msg: "Phone number is required",
        },
      });
    }

    if (this.state.email.length <= 4 || !validateEmail(this.state.email)) {
      return this.setState({
        error: {
          target: "email",
          msg: "Please enter a email",
        },
      });
    }

    // check for errors
    if (this.state.password.length <= 4) {
      return this.setState({
        error: {
          target: "password",
          msg: "Password is required",
        },
      });
    }

    // check for errors
    if (this.state.re_password !== this.state.password) {
      return this.setState({
        error: {
          target: "re_password",
          msg: "Passwords does not match",
        },
      });
    }

    this.setState({
      loading: true,
      error: {
        target: "",
        msg: "",
      },
    });
    // send data
    this.props.FC_RegisterAccount(
      {
        n_id: this.state.n_id,
        email: this.state.email,
        phone_number: this.state.phone_number,
        password: this.state.password,
      },
      (status: boolean, error: string) => {
        if (status === false) {
          this.setState({
            error: { target: "all", msg: error },
            loading: false,
          });
        } else {
          this.setState({
            loading: false,
          });
        }
      }
    );
  };

  handleChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
    this.setState({
      ...this.state,
      error: {
        target: "",
        msg: "",
      },
      [e.currentTarget.name]: e.target.value,
    } as Pick<AppState, keyof AppState>);
  };

  submitCodeHandler = (code: number) => {
    this.setState({
      code_loading: true,
      error: {
        target: "",
        msg: "",
      },
    });
    this.props.FC_RegisterCheckCode(
      { n_id: this.props.auth.user.n_id, activation_code: code },
      (status: boolean, msg: string) => {
        if (status === true) {
          this.setState({
            register_success: true,
            loading: false,
            code_loading: false,
          });
        } else {
          this.setState({
            error: {
              target: "all",
              msg: msg,
            },
            loading: false,
            code_loading: false,
          });
        }
      }
    );
  };

  backgroundStyle = {
    backgroundImage: `url(${BgImage})`,
    backgroundPosition: "center",
    backgroundSize: "cover",
    backgroundAttachment: "fixed",
  };

  render() {
    // check if successfully is done
    if (this.state.register_success === true) {
      return (
        <div style={this.backgroundStyle} className="w-full h-screen">
          <div className="max-w-4xl mx-auto bg-opacity-80 bg-green-100 p-6 rounded mt-2 text-center">
            <MdCheckCircle className="text-9xl text-center mx-auto text-green-700" />
            <h1 className="text-3xl text-green-800 font-bold">
              Account created
            </h1>
            <p className="text-green-800 mb-4">
              Your account has been created successfully.
            </p>

            <Link
              className="bg-green-600 text-white py-2 px-5 mt-3 rounded hover:bg-green-500"
              to="/login"
            >
              Login
            </Link>
          </div>
        </div>
      );
    }

    return (
      <div style={this.backgroundStyle}>
        <div className="max-w-7xl mx-auto px-2 sm:px-6 lg:px-24  h-screen flex flex-wrap content-center">
          <div className="grid lg:grid-cols-5 w-full">
            <div className="col-span-3 hidden lg:block">
              {/* <img src={BgImage} alt="isa teacher" /> */}
              <h1 className="text-7xl font-serif">
                Online Teacher <br /> Recruitment <br /> Platform
              </h1>
              <h4 className="text-blue-600 text-3xl w-2/3 mt-2">
                For all public schools under Rwanda Basic Education Board and
                TVET
              </h4>
            </div>

            <div className="col-span-2">
              {this.props.auth.user?.n_id?.length >= 3 ? (
                <div className="">
                  {this.state.error.target === "all" && (
                    <div className="mx-auto w-full">
                      <Alert
                        title={this.state.error.msg}
                        theme={AlertInterface.danger}
                      />
                    </div>
                  )}
                  <ConfirmationCode
                    user={this.props.auth.user}
                    submitCode={this.submitCodeHandler}
                    code_loading={this.state.code_loading}
                  />
                </div>
              ) : (
                <div className="bg-white bg-opacity-70 shadow mx-auto px-4 py-5 rounded max-w-xl mt-6 sm:mx-auto">
                  <form onSubmit={this.registerHandler}>
                    <h1 className="px-2 font-extrabold text-4xl text-blue-500  pt-2 pb-4">
                      Register
                    </h1>

                    {this.state.error.target === "all" && (
                      <div className="p-2">
                        <Alert
                          title={this.state.error.msg}
                          theme={AlertInterface.danger}
                        />
                      </div>
                    )}

                    <div className="p-2">
                      <label htmlFor="n_id" className="d-block">
                        National Id
                      </label>
                      <input
                        type="text"
                        name="n_id"
                        onChange={this.handleChange}
                        className={`input-white w-20 inline-block  ${
                          this.state.error?.target === "n_id"
                            ? "border-red-500 border-2"
                            : "input-border"
                        }`}
                        placeholder="Your National Id"
                        value={this.state.n_id}
                        disabled={this.state.loading}
                      />
                      {this.state.error?.target === "n_id" && (
                        <span className="text-red-600">
                          {this.state.error?.msg}
                        </span>
                      )}
                    </div>

                    <div className="p-2">
                      <label htmlFor="n_id" className="d-block">
                        Phone Number
                      </label>
                      <input
                        type="text"
                        name="phone_number"
                        onChange={this.handleChange}
                        className={`input-white w-20 inline-block  ${
                          this.state.error?.target === "phone_number"
                            ? "border-red-500 border-2"
                            : "input-border"
                        }`}
                        placeholder="Your National Id"
                        value={this.state.phone_number}
                        disabled={this.state.loading}
                      />
                      {this.state.error?.target === "phone_number" && (
                        <span className="text-red-600">
                          {this.state.error?.msg}
                        </span>
                      )}
                    </div>

                    <div className="p-2">
                      <label htmlFor="email" className="d-block">
                        Email
                      </label>
                      <input
                        type="text"
                        name="email"
                        onChange={this.handleChange}
                        className={`input-white w-20 inline-block  ${
                          this.state.error?.target === "email"
                            ? "border-red-500 border-2"
                            : "input-border"
                        }`}
                        placeholder="Your email"
                        value={this.state.email}
                        disabled={this.state.loading}
                      />
                      {this.state.error?.target === "email" && (
                        <span className="text-red-600">
                          {this.state.error?.msg}
                        </span>
                      )}
                    </div>

                    <div className="p-2 mt-2">
                      <label htmlFor="username" className="d-block">
                        Password
                      </label>
                      <input
                        type="password"
                        name="password"
                        onChange={this.handleChange}
                        className={`input-white w-20 inline-block  ${
                          this.state.error?.target === "password"
                            ? "border-red-500 border-2"
                            : "input-border"
                        }`}
                        placeholder="Your username"
                        value={this.state.password}
                        disabled={this.state.loading}
                      />
                      {this.state.error?.target === "password" && (
                        <span className="text-red-600">
                          {this.state.error?.msg}
                        </span>
                      )}
                    </div>

                    <div className="p-2 mt-2">
                      <label htmlFor="re_password" className="d-block">
                        Re-Password
                      </label>
                      <input
                        type="password"
                        name="re_password"
                        onChange={this.handleChange}
                        className={`input-white w-20 inline-block  ${
                          this.state.error?.target === "re_password"
                            ? "border-red-500 border-2"
                            : "input-border"
                        }`}
                        placeholder="Your Password"
                        value={this.state.re_password}
                        disabled={this.state.loading}
                      />
                      {this.state.error?.target === "re_password" && (
                        <span className="text-red-600">
                          {this.state.error?.msg}
                        </span>
                      )}
                    </div>

                    <div className="p-2">
                      <div className="flex mt-2">
                        <div className="flex-1 ">
                          <Link
                            to="/login"
                            className="font-bold text-blue-600 underline"
                          >
                            Login
                          </Link>
                        </div>

                        <div className="flex-1  text-right">
                          <button
                            className="py-2 font-bold  px-8 rounded bg-hero text-white inline-block"
                            disabled={this.state.loading}
                            type="submit"
                          >
                            {this.state.loading === true
                              ? "Creating account..."
                              : "Create account"}
                          </button>
                        </div>
                      </div>
                    </div>
                  </form>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({
  todos,
  auth,
}: StoreState): { todos: Todo[]; auth: Auth } => {
  return { todos: todos, auth: auth };
};

export const RegisterAccount = connect(mapStateToProps, {
  FC_RegisterAccount,
  FC_RegisterCheckCode,
  FC_CleanUserDetails,
})(_RegisterAccount);
