interface QuestionsListInterface {
  windowStatus: {
    profile: boolean,
    questionsList: boolean,
  };
  setQuestionsListWindowStatus: (status: boolean) => void;
  setProfileWindowStatus: (status: boolean) => void;
  selectedQuestion: Interfaces.psychometric_question | null;
  setSelectedQuestion: (question: Interfaces.psychometric_question) => void;
  testAnswers: number;
  answeredQuestions: newAnswer[] | [];
  auth: Auth;
}

