const _QuestionsList = (props: QuestionsListInterface) => {
  const [eventDesc, setEventDesc] = useState(false);
  const answeredPercentage = (props.testAnswers*100)/psychometric_questions_list.length;
  const checkQuestionAnswered = (question: Interfaces.psychometric_question) => {
    return props.answeredQuestions.find(itm => itm.question_id === question.question_id) ? true : false;
  }
  return (
    <div className="h-screen bg-gray-800">
      <div className="flex flex-col justify-between items-center h-full">
        <div className={`bg-gray-800 pb-2 pt-1 px-0 flex flex-row justify-${props.windowStatus.questionsList === true ? "between" : "center"} items-center w-full`}>
          {props.windowStatus.questionsList === false &&
            <div onClick={() => props.setQuestionsListWindowStatus(true)} className="has-tooltip bg-gray-700 p-2 rounded cursor-pointer hover:bg-gray-600">
              <span className="text-3xl text-white font-bold">
                <MdDehaze />
              </span>
              <span className='tooltip rounded shadow-lg px-2 py-1 bg-black bg-opacity-80 text-gray-200 ml-9 -mt-8'>
                Questions list
                <div className="w-3 h-3 -mt-2 rotate-45 bg-black"></div>
              </span>
            </div>
          }
          {props.windowStatus.questionsList === true &&
            <>
              <span className="text-lg text-white font-bold mr-3 ml-2 mt-2">
                Questions
              </span>
              <div className="rounded-full flex justify-center items-center bg-gray-700 p-1 mr-2 mt-2">
                <IoClose onClick={() => props.setQuestionsListWindowStatus(false)} className="text-white text-2xl hover:text-gray-400 cursor-pointer" />
              </div>
            </>
          }
        </div>
        {props.windowStatus.questionsList === true &&
          <div className="flex flex-row justify-between items-center px-2 my-1 w-full">
            <span className="text-gray-300 text-xs">Status</span>
            <span className="text-gray-300 text-xs">{props.testAnswers}/{psychometric_questions_list.length}</span>
          </div>
        }
        {props.windowStatus.questionsList === true &&
          <div className="w-full px-2 pb-2">
            <div className="h-2 bg-gray-600 rounded-full w-full">
              <div className={`${answeredPercentage === 100 ? "bg-green-400" : "bg-gray-400"} h-full rounded-full`} style={{width: `${answeredPercentage}%`}}></div>
            </div>
          </div>
          
        }
        <div className="pb-2 pt-2 w-full pl-2 mb-16 overflow-y-auto bg-gray-800 h-full">
          {psychometric_questions_list.map((item, i) => (
            props.selectedQuestion !== null && props.selectedQuestion.question_id === item.question_id ? 
            <div className="flex flex-row justify-between items-center text-gray-300 text-xs px-1 py-1 font-bold bg-gray-200 hover:bg-gray-200 hover:rounded hover:text-gray-900 cursor-pointer rounded-l-full shadow-md pr-3">
              <div className="flex flex-row justify-left items-center">
                <div className={`${checkQuestionAnswered(item) === true ? "bg-green-700" : "bg-gray-600"} h-8 w-8 flex justify-center items-center rounded-full text-white text-lg`}>{i + 1}</div>
                {props.windowStatus.questionsList === true && <span className="px-2 rounded-full text-sm font-bold text-gray-600">{props.windowStatus.questionsList === true && 'Editing'}</span>}
              </div>
            </div>
            :
            <div onClick={() => props.setSelectedQuestion(item)} className="flex flex-row justify-left items-center text-gray-300 text-xs px-1 py-1 font-bold hover:bg-gray-800 hover:rounded hover:text-gray-400 cursor-pointer pr-3">
              <div className={`${checkQuestionAnswered(item) === true ? "bg-green-700" : "bg-gray-700"} h-8 w-8 flex justify-center items-center rounded-full text-white text-lg`}>{i + 1}</div>
              {props.windowStatus.questionsList === true &&<span className="text-gray-400 px-2 rounded-full font-light text-sm">{props.windowStatus.questionsList === true && checkQuestionAnswered(item) === true ? <span className="font-bold text-green-500">Answered</span> : "Pending"}</span>}
            </div>
          ))}
        </div>
      </div>
    </div>
  )
}


const mapStateToProps = ({
  todos,
  auth,
  doTest,
}: StoreState): { todos: Todo[]; auth: Auth; doTest: questionAnswerInterface } => {
  return { todos: todos, auth: auth, doTest: doTest };
};

const QuestionsList = connect(mapStateToProps, {
  fetchTodos,
  deleteTodo,
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
  getUserAnswer,
  addAnswer,
  deleteAnswers,
  SubmitAnswers,
})(_QuestionsList);

export default QuestionsList;