/**
 * * ****************************** ACTIONS *****************************
 */
export const fetchAuth = () => {
  return async (dispatch: Dispatch) => {
    const response = await axios.get<Auth>(url);

    dispatch<FerchLoginDetails>({
      type: ActionTypes.LOGIN_DETAILS,
      payload: response.data,
    });
  };
};

/**
 * @description check whether the 2 stae factor is enabled or not
 * @returns null
 */
export const FC_GetTwoFactorAuth = () => {
  return async (dispatch: Dispatch) => {
    const res = await axios.get<API_TwoFactorAuth>(`${API.AUTH}/settings/2fa`);

    let status: boolean;
    if (res.data.status === "enabled") {
      status = true;
    } else {
      status = false;
    }

    dispatch<GetTwoFactorAuth>({
      type: ActionTypes.SET_TWO_FACTOR_AUTH,
      payload: status,
    });
  };
};

/**
 * @description Register the account to the api
 * @param account
 * @param ResponseFunc return the error from the API
 * @returns
 */
export const FC_RegisterAccount = (
  account: {
    n_id: string;
    phone_number: string;
    email: string;
    password: string;
  },
  ResponseFunc: Function
) => {
  return async (dispatch: Dispatch) => {
    try {
      const res = await axios.post<API_Register>(
        `${API.AUTH}/users/createacc`,
        account
      );

      console.log({ CODE: res.data?.code });

      dispatch<UserRegisterDetails>({
        type: ActionTypes.USER_REGISTER_DETAILS,
        payload: res.data,
      });

      ResponseFunc(true, "");
    } catch (error) {
      ResponseFunc(false, errorToText(error));
    }
  };
};

/**
 * @description change where the user will receive the code (email | sms)
 * @param type
 * @returns
 */
export const FC_ChangeTwoFactorType = (type: string): ChangeTwoFactorType => {
  return {
    type: ActionTypes.CHANGE_TWHO_FACTOR_TYPE,
    payload: type,
  };
};

/**
 * @description Register the account to the api
 * @param account
 * @param MsgHandler return the error from the API
 * @returns
 */
export const FC_RegisterCheckCode = (
  data: {
    n_id: string;
    activation_code: number;
  },
  MsgHandler: Function
) => {
  return async (dispatch: Dispatch) => {
    try {
      const res = await axios.post<{ message: string }>(
        `${API.AUTH}/users/verifyacc`,
        data
      );

      // send the message
      MsgHandler(true, res.data.message);

      dispatch<CleanUserDetails>({
        type: ActionTypes.CLEAN_USER_DETAILS,
      });
    } catch (error) {
      console.log({ code_error: errorToText(error) });
      MsgHandler(false, errorToText(error));
    }
  };
};

