import React, { Component } from 'react'
import { useState } from 'react';
import { Fragment } from 'react';
import { IoIosBriefcase, IoMdBriefcase } from 'react-icons/io';
import { MdAssignmentInd, MdCheckBox, MdChevronRight, MdClose, MdFilterList, MdRadioButtonUnchecked, MdSchool, MdWork } from 'react-icons/md'
import Modal, { ModalSize, Themes } from '../../../components/Modal/Modal';
import ChooseExamCenter from '../ExamCenter/ChooseExamCenter';
import SchoolLevelsFilter from './SchoolLevelsFilter/SchoolLevelsFilter';
import * as Interfaces from '../../../interfaces/index'
import { FiFilter } from 'react-icons/fi';
import axios from 'axios';
import { API } from '../../../utils/api';
import { PositionAPI } from '../../../interfaces/index';
import searchData from "../../../utils/search";
import { ActionTypes } from '../../../actions';
import Loading from '../../../components/Loading/Loading';

interface ApplicationPositionsProps {
  currentPosition: Interfaces.PositionAPI | null;
  setCurrentPosition: (position: Interfaces.PositionAPI) => void;
}

interface ApplicationPositionsState {
  schoolLevelsFilter: boolean;
  educationCategories: Interfaces.education_category[] | null;
  schoolLevels: Interfaces.school_level[] | null;
  searchLevel: searchItem | null;
  positions: Interfaces.PositionAPI[] | null;
  filteredPositions: Interfaces.PositionAPI[] | null;
  searchPosition: string;
}

export interface searchItem {
  educationCategory: Interfaces.education_category | null;
  schoolLevel: Interfaces.school_level | null;
}

export class ApplicationPositions extends Component<ApplicationPositionsProps, ApplicationPositionsState> {
  constructor(props: ApplicationPositionsProps) {
		super(props);

		this.state = {
      schoolLevelsFilter: true,
      educationCategories: null,
      schoolLevels: null,
      searchLevel: null,
      positions: null,
      filteredPositions: null,
      searchPosition: "",
		};
	}
  setSchoolLevelFilterFn = (status:boolean) => {
    this.setState({ schoolLevelsFilter: status })
  }
  setSearchLevel = (educationCategory: Interfaces.education_category | null, schoolLevel: Interfaces.school_level | null) => {
    this.setState({ searchLevel: { educationCategory: educationCategory, schoolLevel: schoolLevel } })
    this.filterPositions(this.state.positions, { educationCategory: educationCategory, schoolLevel: schoolLevel });
  }

  getEducationCategories = async () => {
    try {
      const res = await axios.get<Interfaces.education_category[]>(`${API.APPLICATION}/educationinstitute`);
      if (res.status === 200) {
        this.setState({ educationCategories: res.data });
      }
    } catch (error) {
      console.log({...error});
    }
  }

  getSchoolsLevels = async () => {
    try {
      const res = await axios.get<Interfaces.school_level[]>(`${API.APPLICATION}/schoollevel`);
      if (res.status === 200) {
        this.setState({ schoolLevels: res.data });
      }
    } catch (error) {
      console.log({...error});
    }
  }

  getPositions = async () => {
    try {
      const res = await axios.get<Interfaces.PositionAPI[]>(`${API.APPLICATION}/position`);
      if (res.status === 200) {
        this.setState({ positions: res.data });
        // this.setState({ filteredPositions: res.data });
      }
    } catch (error) {
      console.log({...error});
    }
  }

  filterPositions = (myDATA: PositionAPI[] | null ,searchItem: searchItem | null) => {
    let result: PositionAPI[] | null = myDATA;
    if (myDATA !== null) { 
      if (searchItem !== null) {
        if (searchItem.educationCategory !== null) {
          result = myDATA.filter(itm => itm.education_institute_id.education_institute_id === searchItem.educationCategory?.education_institute_id);
        }
        if (searchItem.schoolLevel !== null) {
          result = myDATA.filter(itm => itm.school_level_id.school_level_id === searchItem.schoolLevel?.school_level_id);
        }
      }
    }
    return result;
  }

  componentDidMount = () => {
    this.getEducationCategories();
    this.getSchoolsLevels();
    this.getPositions();
  }

  render() {
    localStorage.removeItem(ActionTypes.ADD_ANSWER)
    return (
      <Fragment>
      <div className="w-full">
        <div className="grid md:grid-cols-12">
          {this.state.schoolLevelsFilter === true && 
          <SchoolLevelsFilter 
            educationCategories={this.state.educationCategories}
            schoolLevels={this.state.schoolLevels}
            searchLevel={this.state.searchLevel}
            setSearchLevel={this.setSearchLevel}
            setSchoolLevelFilterFn={this.setSchoolLevelFilterFn} />}
          <div className={`${this.state.schoolLevelsFilter === true ? "col-span-8" : "col-span-12"} h-full`}>
            <div className="bg-white shadow rounded-sm  px-2 py-2 h-full">
              <div onClick={() => this.setSchoolLevelFilterFn(true)} className="flex flex-row justify-start items-center">
                {this.state.schoolLevelsFilter === false &&
                <div className="rounded w-max px-3 py-2 bg-blue-100 text-blue-600 font-bold flex items-center justify-center cursor-pointer hover:bg-blue-100">
                  <FiFilter className="text-3xl md:text-2xl" />
                  <span className="hidden md:block">Filter by School levels</span>
                  <span className="block md:hidden">Filter</span>
                </div>}
                <span className="text-lg my-0 md:my-2 mx-2 font-bold text-blue-600 uppercase">Choose a position for the application</span>
              </div>
              
              <div className="flex flex-row gap-1 mt-2">
                <input onChange={(e) => this.setState({ searchPosition: e.target.value })} value={this.state.searchPosition} type="text" placeholder="Type a position name" className="input-white border border-gray-400 h-10" />
              </div>
              <div className="mt-6">
                <div className="flex flex-col gap-2">
                  {this.filterPositions(this.state.positions, this.state.searchLevel) === null ? <Loading /> : this.filterPositions(this.state.positions, this.state.searchLevel)!.length === 0 ? "No positions available" : 
                  searchData(this.filterPositions(this.state.positions, this.state.searchLevel)!, this.state.searchPosition).map((item, key) => (
                    <div key={key + 1} onClick={() => this.props.setCurrentPosition(item)} className="cursor-pointer rounded  px-3 py-2 border border-gray-200 hover:border-blue-500 hover:bg-blue-100">
                      <div className="grid lg:grid-cols-12">
                        <div className="col-span-12 lg:col-span-10">
                          <div className="flex flex-row items-center gap-3">
                            <div><IoIosBriefcase className="text-4xl md:text-5xl text-blue-600" /></div>
                            <div className="flex flex-col">
                              <span className="text-xs lg:text-sm font-bold text-gray-700">{item.position_title}</span>
                              <span className="text-xs text-gray-500 italic">School level: <span className="text-gray-800 font-light" style={{fontStyle: 'normal'}}>{item.school_level_id.school_level_name}</span></span>
                              <span className="text-xs font-bold text-blue-600 bg-blue-100 w-max px-3 py-1 rounded-full">{item.education_institute_id.education_category_title}</span>
                            </div>
                          </div>
                        </div>
                        <div className="col-span-12 lg:col-span-2 hidden lg:block">
                          <div className="h-full flex items-center justify-end">
                            <div className="btn bg-blue-600 text-white float-right">Choose</div>
                          </div>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      </Fragment>
    )
  }
}

export default ApplicationPositions