import React, { Fragment, useEffect, useState } from 'react'
import { IoIosBriefcase, IoMdBookmark } from 'react-icons/io'
import { MdAddCircle, MdCheckCircle, MdDescription, MdEdit } from 'react-icons/md'
import { RiFileEditFill } from 'react-icons/ri'
import Modal, { ModalSize, Themes } from '../../../components/Modal/Modal'
import { ApplicationStepsTypes } from '../ApplicationSteps/ApplicationSteps'
import * as Interfaces from '../../../interfaces/index';
import { connect } from "react-redux";
import {
  Todo,
  Auth,
  fetchTodos,
  deleteTodo,
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
  getUserAnswer,
  addAnswer,
  getUsersAnswers,
  questionAnswerInterface,
  ActionTypes,
  SubmitAnswers,
} from "../../../actions";
import { StoreState } from "../../../reducers";
import { newAnswer } from '../DoTest/DoTest'
import { psychometric_questions_list } from '../DoTest/QuestionsList/QuestionsList'

interface PositionRequirementProps {
  currentPosition: Interfaces.PositionAPI | null;
  setCurrentPosition: (position: Interfaces.PositionAPI | null) => void;
  setApplicationStep: (step: ApplicationStepsTypes) => void;
  addAnswer: Function;
  doTest: questionAnswerInterface;
  auth: Auth;
  SubmitAnswers: Function;
}

const _PositionRequirement = (props: PositionRequirementProps) => {
  const [requirementsModal, setRequirementsModal] = useState<boolean>(false);
  const findResult = (answers: newAnswer[]) => {
    let marks: number = 0;
    for (let i in answers) {
      if (answers[i].user_answers?.status === true) {
        marks = marks + 1;
      }
    }
    return (marks * 100)/psychometric_questions_list.length;
  }
  let testResults = localStorage.getItem(ActionTypes.ADD_ANSWER);
  let lenghtTestResult = testResults === null ? 0 : JSON.parse(testResults!).questionAnswers.length;
  // localStorage.removeItem(ActionTypes.ADD_ANSWER)
  return (
    <Fragment>
      <div className="bg-white px-2 py-2 rounded shadow">
        {console.log("Test done: ", props.doTest)}
        <div className="flex flex-col bg-blue-100 px-2 py-2 rounded">
          <div className="flex flex-col md:flex-row justify-between items-start md:items-center w-full">
            <div className="flex flex-col md:flex-row items-center w-full">
              <IoIosBriefcase className="hidden md:block text-6xl text-blue-600" />
              <div className="flex flex-col px-0 md:px-2 w-full">
                <span className=" text-xs font-normal text-black-800">Selected position:</span>
                <span className="text-blue-700 font-bold">{props.currentPosition!.position_title}</span>
              </div>
            </div>
            <div className="mt-2 md:mt-0">
              <button onClick={() => props.setCurrentPosition(null)} className="btn bg-white text-blue-500 border border-blue-200 text-center flex justify-center items-center hover:bg-blue-600 hover:text-white">
                <MdEdit /> Change
              </button>
            </div>
          </div>
          <div className="hidden md:block border-2 border-white rounded mt-2 bg-white">
            <div className="grid md:grid-cols-4 text-gray-500 gap-5 py-0 px-0 hover:text-blue-600">
              <div className="col-auto bg-blue-100 rounded-l flex items-center">
                <div className="text-sm font-bold mb-0 text-blue-800 flex items-center ml-4">Exams for the position</div>
              </div>
              {props.currentPosition && props.currentPosition.courses.length === 0 ? "No Courses!" :
              props.currentPosition?.courses.map((item, i) => (
                <div key={i + 1} className="col-auto flex flex-row items-center gap-2 py-2">
                  <div className="h-8 w-8 px-2 py-2 text-lg font-bold bg-gray-200 rounded-full flex items-center justify-center">{i + 1}</div>
                  <div className="flex flex-col"><span className="font-bold">{item.course_name}</span><span className="text-xs text-gray-600">CODE: {item.course_code}</span></div>
                </div>
              ))}
            </div>
          </div>
        </div>
        {/* Mobile view */}
        <div className="block md:hidden border bg-gray-100 mt-2 rounded px-2 py-1">
          <div><span className="font-bold text-gray-700">Exams for the position</span></div>
          <div className="mt-2 text-xs text-gray-600">
            1. Mathematics<br/>
            2. Physics<br/>
            3. English<br/>
          </div>
        </div>

        <div className="mt-4">
          <span className="font-bold text-xl text-gray-600">Requirements for the position</span>
        </div>
        <div className="my-4">

          <div className="flex flex-row justify-between items-center bg-white hover:bg-gray-50 px-2 py-2 my-2 rounded gap-2 cursor-pointer border">
            <div className="flex flex-row items-center gap-2">
              <div><RiFileEditFill className="text-5xl text-gray-400" /></div>
              <div className="flex flex-col px-2">
                <span className="text-lg font-bold text-gray-500">Psychometric Test</span>
                <span className="text-xs text-gray-600">Psychometric tests are used to measure an individual's' mental capabilities and behavior.</span>
              </div>
            </div>
            <div>
              {/* {findResult(props.doTest)} */}
              {props.doTest.user_id !== props.auth.user.n_id || props.doTest.questionAnswers.length < psychometric_questions_list.length ?
              <div onClick={() => props.setApplicationStep(ApplicationStepsTypes.Step2)} className="btn border border-blue-500 flex flex-row items-center text-primary bg-white w-max cursor-pointer">Do test</div>:
              findResult(props.doTest.questionAnswers) < 50 ?
              <div className="bg-red-200 px-3 py-2">{findResult(props.doTest.questionAnswers)}% marks</div>:
              <MdCheckCircle className="text-blue-500 text-5xl" />}
            </div>
          </div>
          {props.currentPosition?.requirements.map((item, i) => (
            <div className="flex flex-row justify-between items-center bg-gray-200 hover:bg-gray-100 px-2 py-2 my-2 rounded gap-2 cursor-pointer">
              <div className="flex flex-row items-center gap-2">
                <div><IoMdBookmark className="text-5xl text-gray-500" /></div>
                <div className="flex flex-col px-2">
                  <span className="text-lg font-bold text-gray-700">Requirements for: {item.title}</span>
                  {item.user_required_docs.map((itm) => (
                    <span className="text-xs text-gray-600">{itm.title}</span>
                  ))}
                </div>
              </div>
              <div>
              {props.doTest.user_id === props.auth.user.n_id && props.doTest.questionAnswers.length > 0 &&
                findResult(props.doTest.questionAnswers) >= 50 &&
                <div onClick={() => setRequirementsModal(true)} className="btn border border-blue-500 flex flex-row items-center text-primary bg-white w-max cursor-pointer">Complete</div>}
              </div>
            </div>
          ))}
        </div>
        <div>
          <div className="flex flex-row justify-between items-center">
            <button onClick={() => props.setCurrentPosition(null)} className="btn bg-white text-gray-600 border border-gray-300">Back</button>
            {props.doTest.user_id === props.auth.user.n_id && props.doTest.questionAnswers.length > 0 ?
              findResult(props.doTest.questionAnswers) >= 50 ?
              <button onClick={() => props.setApplicationStep(ApplicationStepsTypes.Step3)} className="btn bg-green-500 text-white">Choose examination center</button>:
              <div className="bg-red-200 text-red-600 px-3 py-4 text-sm float-right text-right flex items-center">You can not proceed because, you failed the psychometric test with <div className="bg-white border border-red-300 px-6 py-2 text-center w-max rounded float-right ml-3">{findResult(props.doTest.questionAnswers)}%</div></div>:
              ""}
          </div>
        </div>
      </div>
      {requirementsModal === true &&
        <Modal
          title="Add requirement"
          backDrop={true}
          theme={Themes.primary}
          close={() => setRequirementsModal(false)}
          backDropClose={false}
          widthSizeClass={ModalSize.large}
          displayClose={true}
          padding={{title: true, body: true}}
        >
          <div>
            content
          </div>
        </Modal>
      }
    </Fragment>
  )
}

const mapStateToProps = ({
  todos,
  auth,
  doTest,
}: StoreState): { todos: Todo[]; auth: Auth; doTest: questionAnswerInterface } => {
  return { todos: todos, auth: auth, doTest: doTest };
};

const PositionRequirement = connect(mapStateToProps, {
  fetchTodos,
  deleteTodo,
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
  getUserAnswer,
  addAnswer,
  SubmitAnswers,
})(_PositionRequirement);

export default PositionRequirement;