import React, { Fragment } from 'react'
import { useState } from 'react'
import { AiOutlineBars, AiOutlineUser } from 'react-icons/ai'
import { FaRegUser, FaUser } from 'react-icons/fa'
import { IoDocumentTextOutline, IoNotifications } from 'react-icons/io5'
import { MdArrowBack, MdCheckCircle, MdWarning } from 'react-icons/md'
import { ApplicationStepsTypes } from '../../ApplicationSteps/ApplicationSteps'
import { psychometric_questions_list } from '../QuestionsList/QuestionsList'
import * as Interfaces from '../../../../interfaces/index';
import { newAnswer } from '../DoTest'
import Modal, { Themes, ModalSize } from '../../../../components/Modal/Modal'
import { RiErrorWarningFill } from 'react-icons/ri'
import { connect } from "react-redux";
import {
  Todo,
  Auth,
  fetchTodos,
  deleteTodo,
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
  getUserAnswer,
  addAnswer,
  getUsersAnswers,
  addUsersAnswers,
  questionAnswerInterface,
  deleteAnswers,
  SubmitAnswers
} from "../../../../actions";
import { StoreState } from "../../../../reducers";

interface TestTitleInterface { 
  currentPosition: Interfaces.PositionAPI | null;
  setCurrentPosition: (position: Interfaces.PositionAPI | null) => void;
  setApplicationStep: (step: ApplicationStepsTypes) => void;
  windowStatus: {
    profile: boolean,
    questionsList: boolean,
  };
  setProfileWindowStatus: (status: boolean) => void;
  setQuestionsListWindowStatus: (status: boolean) => void;
  testAnswers: number;
  answeredQuestions: newAnswer[] | [];
  setSelectedQuestion: (question: Interfaces.psychometric_question) => void;
  setCompletedTest: (state: boolean) => void;
  deleteAnswers: Function;
  SubmitAnswers: Function;
  auth: Auth
}

const _TestTitle = (props: TestTitleInterface) => {
  const [submitModal, setSubmitModal] = useState(false);
  const [eventDesc, setEventDesc] = useState(false);
  const [incompleteQuestions, setIncompleteQuestions] = useState<Interfaces.psychometric_question[] | []>([]);
  const answeredPercentage = (props.testAnswers*100)/psychometric_questions_list.length;
  const submitExam = (allQuestions: Interfaces.psychometric_question[], answeredQuestions: newAnswer[]) => {
    let incomplete:Interfaces.psychometric_question[] = [];
    for (let i in allQuestions) {
      let itm = answeredQuestions.find(item => item.question_id === allQuestions[i].question_id) && allQuestions[i];
      console.log(itm)
      if (itm === undefined) {
        incomplete.push(allQuestions[i])
      }
    }
    setIncompleteQuestions(incomplete);
    setSubmitModal(true);
  }
  return (
    <Fragment>
    <div className="bg-blue-900">
      {console.log("Incompleted questions: ", incompleteQuestions)}
      <div className="grid grid-cols-6">
        <div className="col-span-6 md:col-span-2 lg:col-span-2 text-white flex flex-row justify-start items-center gap-2">
          <div className="text-white flex flex-row items-center pl-1 py-3 lg:py-0">
            <IoDocumentTextOutline className="text-3xl ml-0" />
            <span className="font-bold mr-3 text-xl ml-1 lg:ml-3">Psychometric Test</span>
          </div>
        </div>
        <div className="col-span-6 md:col-span-4 lg:col-span-4" style={{alignSelf: "center"}}>
          <div className="grid md:grid-cols-8 lg:grid-cols-12">
            <div className="hidden col-span-3 md:flex flex-col justify-center items-center h-full gap-1">
              <div className="text-blue-200 text-xs w-full hidden lg:block">Completed {props.testAnswers} of {psychometric_questions_list.length}</div>
              <div className="bg-blue-600 h-3 w-full rounded-full shadow-md hidden lg:block">
                <div className="bg-blue-400 h-3 w-60 rounded-full shadow-md" style={{width: `${answeredPercentage}%`}}></div>
              </div>
              <div className="h-auto my-2 w-full bg-blue-700 rounded-full flex justify-start items-center shadow-lg lg:hidden">
                <div className="rounded-full bg-blue-500 h-full text-xs flex items-center justify-center font-bold text-white" style={{width: `${answeredPercentage}%`}}>
                  <div className="relative left-0 bg-blue-500 px-1 rounded-full">{answeredPercentage}%</div>
                </div>
              </div>
            </div>
            <div className="col-span-4 hidden lg:block h-full">
              <div className="flex flex-row justify-center items-center h-full">
                <div className="flex flex-col text-xs justify-start items-start text-blue-200">
                  <span>Start at: 12h:00 am</span>
                  <span>End at: 16h:00 am</span>
                </div>
              </div>
            </div>
            <div className="col-span-5 bg-blue-900">
              <div className="flex flex-row justify-between md:justify-end items-center h-full gap-2">
                <div className="flex flex-col text-xs justify-start items-center text-blue-200 pl-2 md:pl-0">
                  <span>Counting down</span>
                  <span className="text-lg lg:text-xl font-bold">12:00:00</span>
                </div>
                <button onClick={() => {submitExam(psychometric_questions_list, props.answeredQuestions)}} type="button" className="bg-green-700 hover:bg-green-600 border border-white text-white rounded flex flex-row justify-center items-center gap-2 px-2 py-2 my-1 mx-2 float-right">
                  <MdCheckCircle className="text-white text-xl" />
                  <span>Submit</span>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    {submitModal === true && 
    <Modal
      title={<div className={`${incompleteQuestions.length === 0 ? "text-blue-600" : "text-gray-800"} font-bold text-xl uppercase`}>Psychometric test submission</div>}
      backDrop={true}
      theme={Themes.default}
      close={() => setSubmitModal(false)}
      backDropClose={false}
      widthSizeClass={ModalSize.large}
      displayClose={true}
      padding={{title: true,}} >
        <div>
          {incompleteQuestions.length !== 0 ? 
          <div>
            <div className="grid md:grid-cols-2">
              <div className="md:col-span-1">
                <div className="flex flex-col justify-center items-center mx-4 mb-3 rounded bg-gray-200 px-2 py-2">
                  <RiErrorWarningFill className="text-8xl text-gray-400" />
                  <div className="text-xl text-gray-700 font-bold mb-2 text-center">There are incomplete questions</div>
                  <div className="text-gray-600 text-xs text-center pb-2">Please, make sure to complete the following remaining questions which are not yet answered. Please, if you proceed anyway, for these questions, you will not be marked!</div>
                </div>
              </div>
              <div className="md:col-span-1">
                <div className=" mr-3">
                  <div className="border-2 border-yellow-500 bg-yellow-50 px-2 py-3 rounded">
                    <div className="text-xl text-center text-gray-700 font-bold">List of incomplete questions</div>
                    <div className="text-xs text-center mb-5 mt-1 text-gray-600">Choose a question number you want to answer directly</div>
                    <div className="flex flex-row items-center justify-center gap-3">
                      {incompleteQuestions.map((item, i) => (
                        <div onClick={() => { props.setSelectedQuestion(item); setSubmitModal(false) }} key={i + 1} className="w-10 h-10 p-4 rounded-full bg-yellow-600 flex justify-center items-center font-bold text-2xl text-white cursor-pointer">{item.question_id}</div>
                      ))}
                    </div>
                  </div>
                  <div className="flex flex-row items-center justify-between mt-5 gap-3">
                    <div></div>
                    <div onClick={() => setSubmitModal(false)} className="bg-gray-500 text-white flex items-center rounded px-3 py-2 cursor-pointer hover:bg-gray-900">Go back</div>
                    {/* <div className="bg-blue-600 text-white flex items-center rounded px-3 py-2 cursor-pointer hover:bg-blue-500">Confirm submission</div> */}
                  </div>
                </div>
              </div>
            </div>
            
          </div>
          : 
          <div className="px-4 pb-2">
            <div className="text-sm text-gray-500">Hey <b>{props.auth.user.fname}</b>, All questions have been answered, are you sure do you want to submit the final answers?</div>
            
            <div className="my-2">
              <div className="font-bold text-lg text-gray-700 my-2 mt-4">Test your answers</div>
              <div className="flex flex-col gap-3">
                {psychometric_questions_list.map((item, i) => (
                  <div className="bg-gray-200 px-2 py-2 rounded">
                    <div className="grid md:grid-cols-12 w-full">
                      <div className="col-span-10 flex flex-row items-center gap-3">
                        <div key={i + 1} className="w-6 h-6 p-2 rounded-full bg-gray-600 flex justify-center items-center font-bold text-lg text-white cursor-pointer">{item.question_id}</div>
                        <div className="text-sm">
                          <div>{item.question_description}</div>
                          <div className="text-blue-700 font-bold text-sm">Answer:&nbsp;
                            {props.answeredQuestions.find(itm => itm.question_id === item.question_id)?.user_answers?.answer}
                          </div>
                        </div>
                      </div>
                      <div onClick={() => { props.setSelectedQuestion(item); setSubmitModal(false) }} className="col-span-2 bg-gray-100 rounded px-2 py-1 text-center float-right cursor-pointer font-bold text-md text-gray-600 border border-gray-400 flex items-center justify-center h-max md:ml-3">Edit</div>
                    </div>
                  </div>
                ))}
              </div>
            </div>

            <div className="grid grid-cols-2 my-4 gap-4">
              <div onClick={() => setSubmitModal(false)} className="col-span-1 bg-white text-gray-700 flex items-center justify-center rounded px-5 py-2 cursor-pointer hover:bg-gray-200 hover:text-black border border-gray-400 hover:shadow-xs">
                <div className="flex flex-col justify-center items-center">
                  <MdArrowBack className="text-5xl" />
                  <span className="text-lg">Back edit</span>
                </div>
              </div>
              <div onClick={() => { setSubmitModal(false); props.setCompletedTest(true); props.SubmitAnswers(props.auth.user.n_id); }} className="col-span-1 bg-blue-100 text-blue-600 flex items-center justify-center rounded px-5 py-2 cursor-pointer hover:bg-blue-600 hover:text-white border border-blue-300 hover:shadow-lg">
                <div className="flex flex-col justify-center items-center">
                  <MdCheckCircle className="text-5xl" />
                  <span className="text-lg">Submit test</span>
                </div>
              </div>
            </div>
          </div>}
        </div>
    </Modal>}
    </Fragment>
  )
}

const mapStateToProps = ({
  todos,
  auth,
  doTest,
}: StoreState): { todos: Todo[]; auth: Auth; doTest: questionAnswerInterface } => {
  return { todos: todos, auth: auth, doTest: doTest };
};

const TestTitle = connect(mapStateToProps, {
  fetchTodos,
  deleteTodo,
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
  getUserAnswer,
  addAnswer,
  deleteAnswers,
  SubmitAnswers,
})(_TestTitle);

export default TestTitle;
