import { Auth, Action, ActionTypes, psychometricAnswer, questionAnswerInterface } from "../actions";
import { APP_TOKEN_NAME } from "../utils/AxiosToken";
import { psychometric_question, question_answer,  } from "../interfaces";
import { newAnswer } from "../containers/Application/DoTest/DoTest";

// default state
const defaultState: questionAnswerInterface = {
  questionAnswers: []
};

/**
 * this is the comment that im using from my brand new keyboard and i like i very mutch hahah this is awesome and coool
 * @param state
 * @param action
 * @returns
 */
export const doTestReducer = (state: questionAnswerInterface = defaultState, action: Action) => {
  switch (action.type) {
    case ActionTypes.GET_USER_ANSWERS:
      if (state.questionAnswers.length === 0) {
        let cashedData: string | null = localStorage.getItem(ActionTypes.ADD_ANSWER);
        return { ...JSON.parse(cashedData!) };
      } else {
        return {
          ...state
        }
      }
    case ActionTypes.ADD_ANSWER:
      state.questionAnswers = state.questionAnswers.filter(itm => itm.question_id !== action.payload.question_id);
      state.questionAnswers.push(action.payload);
      localStorage.setItem(ActionTypes.ADD_ANSWER, JSON.stringify(state));
      return {
        ...state, 
      }
    case ActionTypes.deleteAnswers:
      state.questionAnswers.length = 0;
      return {
        ...state, 
      }
    case ActionTypes.submitAnswersAction:
      state.user_id = action.payload;
      return {
        ...state, 
      }
    default:
      return state;
  }
};
