import React, { FC, Fragment, ReactElement, useState } from "react";
import { CandidateInterface } from "../../interfaces/candidatesInterfaces";
import Alert, { AlertInterface } from "../../components/Alert/Alert";
import { MdAccountCircle } from "react-icons/md";

type ConfirmationCodeProps = {
  user: CandidateInterface;
  submitCode: Function;
  code_loading: boolean;
};

const ConfirmationCode: FC<ConfirmationCodeProps> = ({
  user,
  submitCode,
  code_loading,
}): ReactElement => {
  const [code, setCode] = useState("");
  const [error, setError] = useState("");

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
    setCode(e.target.value);
    setError("");
  };

  const registerHandler = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    // check for errors
    if (code.length <= 4) {
      return setError("Code is required");
    }

    submitCode(+code);
  };

  /* function body */
  return (
    <Fragment>
      <div className="bg-white bg-opacity-70 shadow mx-auto px-4 py-5 rounded max-w-xl mt-6 sm:mx-auto">
        <div className="flex ">
          <div className="flex items-center">
            <MdAccountCircle className="text-7xl text-blue-500" />
          </div>
          <div className="flex-1 pl-3">
            {user.fname !== undefined && user.fname.length >= 1 && (
              <div className="">
                <i className="text-gray-500 text-xs">Names</i>{" "}
                <b className="block text-lg -mt-2">
                  {user.fname} {user.lname}
                </b>
              </div>
            )}

            {user.email !== undefined && user.email.length >= 1 && (
              <div className="">
                <i className="text-gray-500 text-xs">Email</i>
                <b className="block text-lg -mt-2">{user.email} </b>
              </div>
            )}

            {user.district !== undefined && user.district.length >= 1 && (
              <div className="">
                <i className="text-gray-500 text-xs">District</i>
                <b className="block text-lg -mt-2">{user.district} </b>
              </div>
            )}

            {user.n_id !== undefined && user.n_id.length >= 1 && (
              <div className="">
                <i className="text-gray-500 text-xs">NID</i>{" "}
                <b className="block text-lg -mt-2">{user.n_id} </b>
              </div>
            )}

            {user.phone_number !== undefined && user.phone_number.length >= 1 && (
              <div className="">
                <i className="text-gray-500 text-xs">Phone</i>{" "}
                <b className="block text-lg -mt-2">{user.phone_number} </b>
              </div>
            )}

            {user.phone_number !== undefined && user.phone_number.length >= 1 && (
              <div className="">
                <i className="text-gray-500 text-xs">Phone</i>{" "}
                <b className="block text-lg -mt-2">{user.phone_number} </b>
              </div>
            )}

            {user.date_of_birth !== undefined &&
              user.date_of_birth.length >= 1 && (
                <div className="">
                  <i className="text-gray-500 text-xs">DOB</i>{" "}
                  <b className="block text-lg -mt-2">{user.date_of_birth} </b>
                </div>
              )}
          </div>
        </div>
      </div>

      <div className="bg-white bg-opacity-70 shadow mx-auto px-4 py-5 rounded max-w-xl mt-3 sm:mx-auto ">
        <form onSubmit={registerHandler}>
          <p className="p-2">
            To continue logging into your account, enter the code we sent you
          </p>

          {error !== "" && (
            <Alert title={error} theme={AlertInterface.danger} />
          )}

          <div className="p-2">
            <label htmlFor="n_id" className="d-block text-gray-500">
              CODE:
            </label>
            <input
              type="number"
              name="n_id"
              onChange={handleChange}
              className={`input-white w-20 inline-block  ${
                error !== "" ? "border-red-500 border-2" : "input-border"
              }`}
              placeholder="Enter the code"
              value={code}
              disabled={code_loading}
            />
            {error !== "" && <span className="text-red-600">{error}</span>}
          </div>

          <div className="p-2 text-right">
            <button
              className="btn bg-hero text-white inline-block"
              disabled={code_loading}
              type="submit"
            >
              {code_loading === true ? "Veryfing..." : "Check code"}
            </button>
          </div>
        </form>
      </div>
    </Fragment>
  );
};
export default ConfirmationCode;
